:root {
	--main-bg: 255, 255, 255;
	--main-text-color: 25, 25, 25;
	--light-text-color: 190, 190, 190;
	--default-bg-color: 40, 40, 40;
	--light-bg-color: 242, 242, 242;
	--default-border-color: 230, 230, 230;
	--primary-bg-color: 250, 77, 96;
	--secondary-bg-color: 70, 140, 180;
	--action-bg-color: 65, 83, 155;
	--action2-bg-color: 243, 128, 38;
	--default-border-radius: 0.5rem;
}

html[data-theme='dark'] {
	--main-bg: 37, 40, 44;
	--main-text-color: 255, 255, 255;
	--light-bg-color: 46, 49, 54;
	--default-border-color: 55, 58, 63;
	--default-bg-color: 243, 243, 243;
}

* {
	box-sizing: border-box;
}

*:before,
*:after {
	box-sizing: border-box;
}

html {
	box-sizing: border-box;
}

html,
body {
	background-color: rgba(var(--main-bg), 1);
	color: rgba(var(--main-text-color), 1);
	font-family: Inter, sans-serif;
	text-rendering: optimizeLegibility;
	font-size: 1rem;
	margin: 0;
	padding: 0;
}

p {
	font-family: Inter, sans-serif;
	text-rendering: optimizeLegibility;
	font-size: 1rem;
	line-height: 1.5em;
}

a {
	color: rgba(var(--primary-bg-color), 1);
	text-decoration: none;
}

.ms-alert {
	/* General */
	display: inline-block;
	height: auto;
	position: relative;
	box-sizing: border-box;
	vertical-align: middle;
	/* Color */
	background-color: rgba(var(--main-bg), 1);
	color: rgba(var(--alert-text-color), 1);
	/* Font */
	font-size: 0.9rem;
	/* Border */
	border: 2px solid rgba(var(--default-border-color), 1);
	border-radius: var(--default-border-radius);
	/* Margin */
	margin: 0.5rem 0;
	padding: 1rem 1.4rem;
	/* Options */
	/* Close */
}
.ms-alert p {
	margin: 0;
}
.ms-alert.ms-primary {
	background-color: rgba(var(--primary-bg-color), 1);
	border-color: transparent;
	color: #fff;
}
.ms-alert.ms-primary .ms-close:before,
.ms-alert.ms-primary .ms-close:after {
	background-color: #fff;
}
.ms-alert.ms-secondary {
	background-color: rgba(var(--secondary-bg-color), 1);
	border-color: transparent;
	color: #fff;
}
.ms-alert.ms-secondary .ms-close:before,
.ms-alert.ms-secondary .ms-close:after {
	background-color: #fff;
}
.ms-alert.ms-action {
	background-color: rgba(var(--action-bg-color), 1);
	border-color: transparent;
	color: #fff;
}
.ms-alert.ms-action .ms-close:before,
.ms-alert.ms-action .ms-close:after {
	background-color: #fff;
}
.ms-alert.ms-action2 {
	background-color: rgba(var(--action2-bg-color), 1);
	border-color: transparent;
	color: #fff;
}
.ms-alert.ms-action2 .ms-close:before,
.ms-alert.ms-action2 .ms-close:after {
	background-color: #fff;
}
.ms-alert.ms-light {
	background-color: rgba(var(--light-bg-color), 1);
	border-color: transparent;
	color: rgba(var(--alert-text-color), 1);
	/* Color */
}
.ms-alert.ms-light .ms-close:before,
.ms-alert.ms-light .ms-close:after {
	background-color: rgba(var(--alert-text-color), 1);
}
.ms-alert.ms-light.ms-primary {
	background-color: rgba(var(--primary-bg-color), 0.2);
	border-color: transparent;
	color: rgba(var(--primary-bg-color), 1);
}
.ms-alert.ms-light.ms-primary .ms-close:before,
.ms-alert.ms-light.ms-primary .ms-close:after {
	background-color: rgba(var(--primary-bg-color), 1);
}
.ms-alert.ms-light.ms-secondary {
	background-color: rgba(var(--secondary-bg-color), 0.2);
	border-color: transparent;
	color: rgba(var(--secondary-bg-color), 1);
}
.ms-alert.ms-light.ms-secondary .ms-close:before,
.ms-alert.ms-light.ms-secondary .ms-close:after {
	background-color: rgba(var(--secondary-bg-color), 1);
}
.ms-alert.ms-light.ms-action {
	background-color: rgba(var(--action-bg-color), 0.2);
	border-color: transparent;
	color: rgba(var(--action-bg-color), 1);
}
.ms-alert.ms-light.ms-action .ms-close:before,
.ms-alert.ms-light.ms-action .ms-close:after {
	background-color: rgba(var(--action-bg-color), 1);
}
.ms-alert.ms-light.ms-action2 {
	background-color: rgba(var(--action2-bg-color), 0.3);
	border-color: transparent;
	color: rgba(var(--action2-bg-color), 1);
}
.ms-alert.ms-light.ms-action2 .ms-close:before,
.ms-alert.ms-light.ms-action2 .ms-close:after {
	background-color: rgba(var(--action2-bg-color), 1);
}
.ms-alert.ms-fullwidth {
	display: block;
	text-align: center;
}
.ms-alert.ms-small {
	padding: 0.7rem 1rem;
}
.ms-alert.ms-medium {
	padding: 1.2rem 1.8rem;
	font-size: 1.1rem;
}
.ms-alert.ms-large {
	padding: 1.5rem 2rem;
	font-size: 1.2rem;
}
.ms-alert .ms-close {
	/* General */
	width: 20px;
	height: 20px;
	display: block;
	/* Position */
	position: absolute;
	top: 2px;
	right: 2px;
	/* Text */
	text-align: center;
	/* Cursor */
	cursor: pointer;
}
.ms-alert .ms-close:before,
.ms-alert .ms-close:after {
	/* General */
	height: 10px;
	width: 2px;
	/* Position */
	position: absolute;
	left: 9px;
	top: 5px;
	/* Content */
	content: ' ';
	/* Color */
	background-color: rgba(var(--main-text-color), 1);
}
.ms-alert .ms-close:before {
	transform: rotate(45deg);
}
.ms-alert .ms-close:after {
	transform: rotate(-45deg);
}
.ms-alert .ms-close:hover {
	background-color: rgba(0, 0, 0, 0.1);
	border-radius: var(--default-border-radius);
}
.ms-alert .ms-close:active {
	background-color: rgba(0, 0, 0, 0.2);
	border-radius: var(--default-border-radius);
}

.ms-article {
