	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background: rgb(2, 0, 36);
	background: linear-gradient(
		0deg,
		rgb(2, 0, 36) 0%,
		rgba(0, 0, 0, 0.7) 0%,
		rgba(0, 0, 0, 0) 100%
	);
}
.ms-article .ms-article-picture .ms-article-picture-title {
	position: absolute;
