.ms-article .ms-article-picture .ms-article-picture-title h1,
.ms-article .ms-article-picture .ms-article-picture-title h2,
.ms-article .ms-article-picture .ms-article-picture-title h3,
.ms-article .ms-article-picture .ms-article-picture-title h4,
.ms-article .ms-article-picture .ms-article-picture-title h5,
.ms-article .ms-article-picture .ms-article-picture-title h6,
