	text-decoration: none;
	font-weight: bold;
	color: rgba(255, 255, 255, 0.95);
	margin: 0;
	padding: 0;
}
.ms-article .ms-article-infos {
	margin: 0;
	padding: 0.4rem 0;
}
.ms-article .ms-article-infos ul {
	list-style: none;
	margin: 0;
	padding: 0;
}
.ms-article .ms-article-infos ul li {
	display: inline-block;
