	font-size: 14px;
	margin: 0 1rem 0 0;
	padding: 0;
}
.ms-article .ms-article-infos i {
	color: rgba(var(--main-text-color), 0.5);
}
.ms-article .ms-article-title {
	display: block;
	margin: 0;
	padding: 1rem 0;
}
.ms-article .ms-article-title h1,
.ms-article .ms-article-title h2,
.ms-article .ms-article-title h3,
.ms-article .ms-article-title h4,
.ms-article .ms-article-title h5,
.ms-article .ms-article-title h6,
.ms-article .ms-article-title a {
	display: block;
	font-size: 2rem;
	text-decoration: none;
	font-weight: bold;
	color: rgba(var(--main-text-color), 1);
	margin: 0;
	padding: 0;
}
.ms-article .ms-article-text {
	color: rgba(var(--main-text-color), 0.9);
	margin: 0;
	padding: 1rem 0;
}
.ms-article .ms-article-text p {
	margin: 0;
	padding: 0;
}
.ms-article .ms-article-text h1,
.ms-article .ms-article-text h2,
.ms-article .ms-article-text h3,
.ms-article .ms-article-text h4,
.ms-article .ms-article-text h5,
.ms-article .ms-article-text h6 {
	display: block;
	font-size: 1.7rem;
	text-decoration: none;
	font-weight: 600;
	color: var(--main-text-color);
	margin: 1rem 0;
	padding: 0;
}
.ms-article .ms-list-label {
	margin: 0.5rem 0 0 0;
	padding: 0;
}
.ms-article .ms-list-label ul {
	list-style: none;
	margin: 0;
	padding: 0;
