.ms-avatar {
	/* General */
	width: 55px;
	height: 55px;
	display: inline-block;
	position: relative;
	/* Colors */
	background-color: rgba(var(--light-bg-color), 1);
	/* Border */
	border-radius: 50%;
	/* Text */
	vertical-align: middle;
	text-align: center;
	/* Margin */
	margin: 0;
	padding: 0;
}
.ms-avatar.ms-border {
	border: 3px solid rgba(var(--default-border-color), 1);
}
.ms-avatar img,
.ms-avatar .ms-avatar-img {
	/* General */
	display: block;
	max-width: 100%;
	min-width: 100%;
	/* Border */
	border-radius: 50%;
	/* Margin */
	margin: 0 auto;
}
.ms-avatar .ms-text {
	font-size: 1.7rem;
	line-height: 55px;
	font-weight: 600;
	color: rgba(var(--main-text-color), 1);
}
.ms-avatar.ms-small {
	width: 35px;
	height: 35px;
}
.ms-avatar.ms-small .ms-text {
	font-size: 1.1rem;
	line-height: 35px;
}
.ms-avatar.ms-medium {
	width: 75px;
	height: 75px;
}
.ms-avatar.ms-medium .ms-text {
	font-size: 2.5rem;
	line-height: 75px;
}
.ms-avatar.ms-medium .ms-status {
	width: 15px;
	height: 15px;
	right: 4px;
	bottom: 4px;
}
.ms-avatar.ms-large {
	width: 95px;
	height: 95px;
}
.ms-avatar.ms-large .ms-text {
	font-size: 3.3rem;
	line-height: 95px;
}
.ms-avatar.ms-large .ms-status {
	width: 20px;
	height: 20px;
	right: 2px;
	bottom: 2px;
}
.ms-avatar .ms-status {
	width: 15px;
	height: 15px;
	position: absolute;
	right: -5px;
	bottom: -5px;
	background-color: rgb(243, 243, 243);
	border: 3px solid rgba(var(--default-border-color), 1);
	overflow: hidden;
	border-radius: 50%;
}
.ms-avatar .ms-status.ms-on {
	background-color: rgb(60, 168, 118);
}
.ms-avatar .ms-status.ms-off {
	background-color: rgb(206, 65, 76);
}
.ms-avatar .ms-status.ms-out {
	background-color: rgb(255, 183, 3);
}

.ms-breadcrumb {
	/* Margin */
	padding: 1rem;
	/* Border */
	border-radius: var(--default-border-radius);
}
.ms-breadcrumb ul {
	/* General */
	list-style: none;
	/* Margin */
	margin: 0;
	padding: 0;
}
.ms-breadcrumb ul li {
	/* General */
	list-style: none;
	display: inline-block;
	/* Color */
	color: rgba(var(--main-text-color), 1);
	/* Font */
	font-weight: 500;
	/* Margin */
	margin: 0 0 0 0.5rem;
	padding: 0;
}
.ms-breadcrumb ul li a:hover {
	color: rgba(var(--primary-bg-color), 0.7);
}
.ms-breadcrumb ul li:after {
	content: '/';
	padding-left: 0.5rem;
}
.ms-breadcrumb ul li:last-child:after {
	content: '';
}
.ms-breadcrumb ul li:first-child {
	margin-left: 0;
}
.ms-breadcrumb.ms-background {
	/* General */
	width: 100%;
	/* Color */
	background-color: rgba(var(--light-bg-color), 1);
}
.ms-breadcrumb.ms-border {
	/* Genral */
	width: 100%;
	/* Border */
	border-bottom: 2px solid rgba(var(--default-border-color), 1);
	border-radius: 0;
}

.ms-browser {
	background-color: rgba(var(--light-bg-color), 1);
	border: 4px solid rgba(var(--light-bg-color), 1);
	border-top-left-radius: var(--default-border-radius);
	border-top-right-radius: var(--default-border-radius);
}
.ms-browser .ms-tab-browser {
	padding: 0.6rem 0.6rem;
}
.ms-browser .ms-tab-browser .ms-dots {
	display: inline-block;
}
.ms-browser .ms-tab-browser .ms-dots .ms-dot {
	display: inline-block;
	height: 12px;
	width: 12px;
	background-color: rgba(var(--primary-bg-color), 1);
	border-radius: 50%;
	margin-right: 2px;
}
.ms-browser .ms-tab-browser .ms-dots .ms-dot.red {
	background-color: #e04540;
}
.ms-browser .ms-tab-browser .ms-dots .ms-dot.yellow {
	background-color: #f9bf10;
}
.ms-browser .ms-tab-browser .ms-dots .ms-dot.green {
	background-color: #3ab656;
}
.ms-browser .ms-tab-browser .ms-uri {
	width: 80%;
	display: inline-block;
	background-color: rgba(var(--main-bg), 1);
	border-radius: 50rem;
	margin: 0 0 0 0.5rem;
	padding: 0.2rem 1rem;
}
.ms-browser .ms-content {
	background-color: rgba(var(--main-bg), 1);
	box-sizing: border-box;
	overflow: hidden;
	padding: 15px;
}

button,
.ms-btn,
input[type='button'],
input[type='submit'] {
	/* General */
	cursor: pointer;
	display: inline-block;
	box-sizing: border-box;
	-webkit-user-select: none;
	-moz-user-select: none;
	user-select: none;
	outline: none;
	/* Color */
