.ms-hero {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: rgba(var(--light-bg-color), 0.5);
}
.ms-hero .ms-hero-background {
	z-index: 1;
	width: 100%;
	height: 100%;
	position: absolute;
	overflow: hidden;
}
.ms-hero .ms-hero-background .ms-hero-cover {
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	position: absolute;
	overflow: hidden;
	background: rgb(2, 0, 36);
	background: linear-gradient(
		0deg,
		rgb(2, 0, 36) 0%,
		rgba(0, 0, 0, 0.7) 0%,
		rgba(0, 0, 0, 0) 100%
	);
}
.ms-hero .ms-hero-background img {
	-o-object-fit: fill;
	object-fit: fill;
}
.ms-hero .ms-hero-body {
	z-index: 2;
