	border-radius: var(--default-border-radius);
	/* Text */
	line-height: 0.9rem;
	font-size: 0.9rem;
	/* Margins */
	margin: 0 0.1rem;
	padding: 0.1rem 0.4rem;
	/* Close */
}
.ms-label.ms-label-active {
	box-shadow: 0 0 0 5px rgba(var(--light-bg-color), 0.3);
}
.ms-label a {
