	color: #ffffff;
	border-color: transparent;
}
.ms-label.ms-primary a {
	color: #ffffff;
}
.ms-label.ms-primary.ms-label-active {
	box-shadow: 0 0 0 5px rgba(var(--primary-bg-color), 0.3);
}
.ms-label.ms-secondary {
	background-color: rgba(var(--secondary-bg-color), 1);
	color: #ffffff;
	border-color: transparent;
}
.ms-label.ms-secondary a {
	color: #ffffff;
}
.ms-label.ms-secondary.ms-label-active {
	box-shadow: 0 0 0 5px rgba(var(--secondary-bg-color), 0.2);
}
.ms-label.ms-action {
	background-color: rgba(var(--action-bg-color), 1);
	color: #ffffff;
	border-color: transparent;
}
.ms-label.ms-action a {
	color: #ffffff;
}
.ms-label.ms-action.ms-label-active {
	box-shadow: 0 0 0 5px rgba(var(--action-bg-color), 0.2);
}
.ms-label.ms-action2 {
	background-color: rgba(var(--action2-bg-color), 1);
	color: #000000;
	border-color: transparent;
}
.ms-label.ms-action2 a {
	color: #ffffff;
}
.ms-label.ms-action2.ms-label-active {
	box-shadow: 0 0 0 5px rgba(var(--action2-bg-color), 0.2);
}
.ms-label.ms-border.ms-primary {
	border: 2px solid rgba(var(--primary-bg-color), 1);
	color: rgba(var(--primary-bg-color), 1);
	background-color: transparent;
}
.ms-label.ms-border.ms-secondary {
	border: 2px solid rgba(var(--secondary-bg-color), 1);
	color: rgba(var(--secondary-bg-color), 1);
	background-color: transparent;
}
.ms-label.ms-border.ms-action {
	border: 2px solid rgba(var(--action-bg-color), 1);
	color: rgba(var(--action-bg-color), 1);
	background-color: transparent;
}
.ms-label.ms-border.ms-action2 {
	border: 2px solid rgba(var(--action2-bg-color), 1);
	color: rgba(var(--action2-bg-color), 1);
	background-color: transparent;
}
.ms-label.ms-light.ms-primary {
	background-color: rgba(var(--primary-bg-color), 0.3);
	border-color: transparent;
	color: rgba(var(--primary-bg-color), 1);
}
.ms-label.ms-light.ms-secondary {
	background-color: rgba(var(--secondary-bg-color), 0.3);
	border-color: transparent;
	color: rgba(var(--secondary-bg-color), 1);
}
.ms-label.ms-light.ms-action {
	background-color: rgba(var(--action-bg-color), 0.3);
	border-color: transparent;
	color: rgba(var(--action-bg-color), 1);
}
.ms-label.ms-light.ms-action2 {
	background-color: rgba(var(--action2-bg-color), 0.3);
	border-color: transparent;
	color: rgba(var(--action2-bg-color), 1);
}
.ms-label.ms-small {
	font-size: 0.7rem;
	border-radius: var(--default-border-radius);
	padding: 0.1rem 0.3rem;
}
.ms-label.ms-medium {
	font-size: 1rem;
	padding: 0.3rem 0.7rem;
}
.ms-label.ms-large {
	font-size: 1.2rem;
	padding: 0.5rem 0.9rem;
}
.ms-label.ms-rounded {
	border-radius: 50rem;
}
.ms-label .ms-close {
	/* General */
	height: 15px;
	width: 15px;
	position: relative;
	display: inline-block;
	overflow: hidden;
	margin-left: 2px;
	/* Text */
	text-align: center;
	/* Cursor */
	cursor: pointer;
}
.ms-label .ms-close:before,
.ms-label .ms-close:after {
	/* General */
	height: 10px;
	width: 2px;
	/* Position */
	position: absolute;
	left: 6.7px;
	top: 2.5px;
	/* Content */
	content: ' ';
	/* Color */
	background-color: rgba(var(--main-text-color), 1);
}
.ms-label .ms-close:before {
	transform: rotate(45deg);
}
.ms-label .ms-close:after {
	transform: rotate(-45deg);
}
.ms-label .ms-close:hover {
	background-color: rgba(0, 0, 0, 0.1);
	border-radius: var(--default-border-radius);
}
.ms-label .ms-close:active {
	background-color: rgba(0, 0, 0, 0.2);
	border-radius: var(--default-border-radius);
}

.ms-loading {
	width: 25px;
	height: 25px;
	border-radius: 50%;
	border: 5px solid rgba(var(--light-bg-color), 1);
	border-top: 5px solid rgba(var(--default-bg-color), 1);
	animation: spin 0.5s linear infinite;
}
.ms-loading.ms-small {
	width: 10px;
	height: 10px;
	border: 3px solid rgba(var(--light-bg-color), 1);
	border-top: 3px solid rgba(var(--default-bg-color), 1);
}
.ms-loading.ms-medium {
	width: 45px;
	height: 45px;
	border: 8px solid rgba(var(--light-bg-color), 1);
	border-top: 8px solid rgba(var(--default-bg-color), 1);
}
.ms-loading.ms-large {
	width: 70px;
	height: 70px;
	border: 10px solid rgba(var(--light-bg-color), 1);
	border-top: 10px solid rgba(var(--default-bg-color), 1);
}
.ms-loading.ms-primary {
	border-top-color: rgba(var(--primary-bg-color), 1);
}
.ms-loading.ms-secondary {
	border-top-color: rgba(var(--secondary-bg-color), 1);
}
.ms-loading.ms-action {
	border-top-color: rgba(var(--action-bg-color), 1);
}
.ms-loading.ms-action2 {
	border-top-color: rgba(var(--action2-bg-color), 1);
}
@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
.ms-menu {
	/* General */
	z-index: 999;
	width: 100%;
	height: 50px;
