	line-height: 50px;
	text-align: center;
	/* Color */
	background-color: rgba(var(--light-bg-color), 1);
}
.ms-menu .ms-menu-logo {
	/* General */
	width: auto;
	height: 50px;
	float: left;
	/* Colors */
	color: rgba(var(--main-text-color), 1);
	/* Margin */
	margin: 0 0 0 15px;
	padding: 0;
}
.ms-menu .ms-menu-logo p {
	font-size: 1.2rem;
	font-weight: 600;
	margin: 0;
	padding: 0;
}
.ms-menu .ms-menu-logo img {
	max-height: 50px;
}
.ms-menu .ms-menu-link {
	width: auto;
	height: 100%;
	float: right;
}
.ms-menu .ms-menu-link ul {
	height: inherit;
	list-style: none;
	text-align: right;
	margin: 0 15px 0 0;
	padding: 0;
}
.ms-menu .ms-menu-link ul li {
	font-weight: 400;
	height: inherit;
	display: inline-block;
	margin: 0;
	padding: 0;
}
.ms-menu .ms-menu-link ul li:last-child {
	padding-right: 0;
}
.ms-menu .ms-menu-link ul li a {
	/* Animation */
	transition: 0.2s ease all;
	color: rgba(var(--main-text-color), 1);
	text-decoration: none;
	font-weight: 600;
	padding: 0 20px;
}
.ms-menu .ms-menu-link ul li a:hover {
	color: rgba(var(--main-text-color), 0.6);
}
.ms-menu .ms-menu-link ul li a:active {
	color: rgba(var(--main-text-color), 0.4);
}
.ms-menu #ms-menu-toggle {
	display: none;
}
.ms-menu .ms-menu-icon {
	display: none;
	cursor: pointer;
	color: rgba(var(--main-text-color), 1);
}

